import React, { useState, useEffect } from "react";
import axios from "axios";
import styled from "styled-components";
import AdminHeader from "../../components/AdminHeader";

const AdminFilter = () => {
  const [title, setTitle] = useState("");

  const onChangeTitle = (e) => {
    setTitle(e.target.value);
  };
  const titleSearchEvent = () => {
    console.log(title);
  };
  const enterEvent = (e) => {
    if (e.key === "Enter") {
      titleSearchEvent();
    }
  };

  return (
    <Container>
      <AdminHeader />
      <ContentContainer>
        <TitleInputArea>
          <TitleInput
            type="text"
            placeholder="여행지를 입력해주세요."
            onChange={onChangeTitle}
            onKeyUp={enterEvent}
          />
          <TitleSearchBtn
            src={require("../../assets/image/graySearchIcon.png")}
            onClick={() => titleSearchEvent()}
          />
        </TitleInputArea>
      </ContentContainer>
    </Container>
  );
};
const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 1920px;
  height: 100%;
  margin: 0 auto;
  justify-content: center;
  position: relative;
  padding: 0px 50px;
`;
const ContentContainer = styled.div`
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  margin: 40px 40px;
`;
const TitleInputArea = styled.div`
  width: 612px;
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
`;
const TitleInput = styled.input`
  width: 100%;
  height: 100%;
  font-size: 14px;
  padding: 7.5px 30px 7.5px 10px;
  border-radius: 8px;
  border: 1px solid #ddd;
`;
const TitleSearchBtn = styled.img`
  position: absolute;
  right: 7.5px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  cursor: pointer;
`;

export default AdminFilter;
